body{
    text-align: center;
}

form{
    display: inline-block;
    border-radius: 8px;
    border: none;
}
input{
    width: 70%;
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border-radius: 8px;
}
textarea{
    border-radius: 8px;
}
.contactin{
    font-family: "Andale Mono", monospace;
    font-size: 20px;
    font-weight: normal;
    text-align: center;
    color: white;
}
.headContact{
    font-family: "Andale Mono", monospace;
    font-variant: small-caps;
    font-size: 34px;
    font-weight: bold;
    text-align: center;
    color: white;
}
.buttonDaddy{
    background: pink;
    color: black;
    padding: 16px 25px;
    border: none;
    cursor: pointer;
    width: 40%;
    border-radius: 8px;
    font-family: "Andale Mono", monospace;
    font-weight: bold;
    font-size: 15px;
    display: block;
    margin: 0 auto;
}
