body { 
       height: 100%; 
       margin: 0; 
       background: url("zelda.jpg") no-repeat center center fixed; 
       background-size: cover;
}
h4{
  font-family: "Andale Mono", monospace;
  font-variant: small-caps;
  font-size: 34px;
  font-weight: bold;
  text-align: center;
  color: white;
}
p{
  font-family: "Andale Mono", monospace;
  font-size: 18px;
  color: white;
}
.content {
        min-height: 100%;
      }
      .content-inside {
        padding: 20px;
        padding-bottom: 50px;
      }
      .header {
        padding: 60px;
        background-color:rgb(0,0,0, .2);
        color: rgb(255,255,255);
      }
      .header a{
        display: block;
        width: 150px;
        color: pink;
        text-decoration: none;
        font-weight: bolder;
        text-transform: uppercase;
        font-family: "Andale Mono", monospace;
        font-size: 30px;
        -webkit-transition: opacity 1s ease-in-out;
        -moz-transition: opacity 1s ease-in-out;
        -ms-transition: opacity 1s ease-in-out;
        -o-transition: opacity 1s ease-in-out;
        transition: opacity 1s ease-in-out;
        opacity: 1;
      }
      .header li{
        display:inline-block;
        padding:6px;
        border-width:0px;
      }
      .header a:hover {
        opacity: 0.1;
        background-color: black;   
    }

      .footer{
        background: rgb(0,0,0);
        color: pink;
        padding: 25px 50px;
        margin-top: -50px;
        text-align: center;
      }
      .footer h1{
        font-weight: bold;
        font-family: "Andale Mono", monospace;
        text-transform: uppercase;
        font-size: 30px;
        text-align: center;
      }
      .footer a{
        margin: 0 1rem;
        transition: transform 250ms;
        display: inline-block;
        color: pink;
      }
      .footer a:hover{
        transform: translateY(-2px);
      }
      .legalRight{
        font-family: "Andale Mono", monospace;
        text-transform: uppercase;
      }
      .videoTag{
      float: left;
      position: absolute;
      width: 50%;
      height: 100px;
      left: -200px;
    }
    .videoTag2{
      float: right;
      position: absolute;
      width: 50%;
      height: 100px;
      right: -200px;
    }
 