.content {
    min-height: 100%;
  }

.content-inside{
    box-sizing: border-box;
}

.box1{
    display: grid;
    grid-template-columns: 50% 50%;
    grid-auto-flow: row;
    grid-gap: 10px;
    padding: 10px;
    
}
ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
}
.skill{
  font-family: "Andale Mono", monospace;
  font-variant: small-caps;
  font-size: 20px;
  font-weight: 900;
  color: white;
}
.list{
    font-family: "Andale Mono", monospace;
    font-size: 16px;
    color:pink;
}

